import * as React from "react";
import { generateMaze } from "../../lib/generate_maze";
import { genIndexes, getParameterByName } from "../../lib/misc";
import { Maze } from "../Maze/Maze";

import styles from "./App.scss";
import "./resources/fonts/stylesheet.css";
import "./resources/fonts/fontello/css/fontello.css";

export type Coor = {y: number,x: number};

export class App extends React.Component<{},{}>
{
    state = {
        counter: 0
    };

    render()
    {
        const width = 20;
        const height = 20;
        const radius = 3;
        const lanternRadius = 5;
        const _stepsPerMaze = getParameterByName("stepsPerMaze");
        let stepsPerMaze = 190;
        if (_stepsPerMaze!==null)
        {
            const __stepsPerMaze = parseInt(_stepsPerMaze);
            if ((!isNaN(__stepsPerMaze)) && (__stepsPerMaze>0))
            {
                stepsPerMaze = __stepsPerMaze;
            }
        }
        
        const lanternsPerMaze = 2;
        
        const { vertSep,horiSep } = generateMaze(width,height);

        const restartHandler = () => 
        {
            this.setState({
                count: this.state.counter+1
            });
        };
    
        return (
            <div className={styles.framev}>
                <div className={styles.frameh}>
                    <Maze 
                        width={20} 
                        height={20} 
                        size={26} 
                        stepsPerMaze={stepsPerMaze}
                        lanternsPerMaze={lanternsPerMaze}
                        radius={radius} 
                        lanternRadius={lanternRadius}
                        vertSep={vertSep} 
                        horiSep={horiSep} 
                        onRestart={restartHandler} 
                    />
                </div>
            </div>
        );
    }
}